*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;

}

.home, .tipografia, .color, .logotipo, .familia {
  display: flex;
  height: 30rem;
  align-items: center;
  justify-content: center ;
  
  font-size: 3rem;
  z-index: -2;

}


.navbar {
    background-image: url(/---/static/media/imagen.94210aa2.png);
    height: 60px;
    display: flex;
    justify-content: start;
    z-index: 1;
}

.nav-menu{
    background-color: #0f1f3b;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: -1rem;
    left: -100%;
    transition:850ms;
    z-index: 1;
}
.menu-bars{
    margin-left: 2rem;
    font-size:  2rem;
    background:none;
    margin-top: 1rem;
    z-index: 1;
}

.close{
    margin-left: -.3rem;
    font-size:  3rem;
    background:none;
    margin-top: .5rem;
    z-index: 3;
}


.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding : 8px 0px 8px 16px;
    list-style: none;
    height: 60px;

}

.nav-text a{
    text-decoration: none;
    color:#f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover{
    background-color: #51a8b1;

}

.nav-menu-items {
    width: 100%;

}

.navbar-toggle{
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}
